@import url(https://fonts.googleapis.com/css?family=Quicksand:300,400,500,700);
body {
  background: -webkit-gradient(linear, left top, right top, from(#ff5722e6), to(#00C4E1));
  background: -webkit-linear-gradient(left, #ff5722e6, #00C4E1);
  background: -o-linear-gradient(left, #ff5722e6, #00C4E1);
  background: linear-gradient(to right, #ff5722e6, #00C4E1);
  color: #111111;
  font-family: 'Quicksand', sans-serif;
}

.weatherApp {
  padding-top: 4%;
}
.weatherApp .weatherQuery input {
  font-size: 20px;
  font-weight: 300;
  font-family: 'Quicksand', sans-serif;
  background: #111111;
  text-align: center;
  padding: 20px 0;
  border: 0px;
  outline: 0px;
  color: white;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}
.weatherApp .weatherInfo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.weatherApp .weatherInfo .cityName div {
  font-size: 32px;
  padding: 20px;
  text-align: center;
  font-weight: 500;
}
.weatherApp .weatherInfo .cityName div span {
  font-weight: 300;
}
.weatherApp .weatherInfo .tempInfo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: stretch;
      align-items: stretch;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.weatherApp .weatherInfo .tempInfo div {
  font-size: 14px;
  text-align: center;
  background: rgba(255, 255, 255, 0.3);
  padding: 50px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  min-width: 100px;
  margin: 1% 10%;
}
.weatherApp .weatherInfo .tempInfo div span {
  display: block;
  font-size: 26px;
}
.weatherApp .info {
  text-align: center;
  margin: 60px 0;
  font-weight: bold;
}



